import { Component, ViewChild } from '@angular/core';
import { Events, MenuController, Nav, Platform,ModalController } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { LoginPage } from '../pages/login/login';
import {ModalPage} from '../pages/modal/modal';

import { ToastController } from 'ionic-angular';

export interface PageInterface {
  title: string;
  name: string;
  component: any;
  icon: string;
  logsOut?: boolean;
  index?: number;
  tabName?: string;
  tabComponent?: any;
}

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

/*appPages: PageInterface[] = [
    { title: 'noticias', name: 'ListaCelulas', component: ListaCelulas, index: 0, icon: 'calendar' },
    { title: 'Notícias UFC', name: 'ListaNoticiasPage', component: ListaNoticiasPage, index: 1, icon: 'list-box'},
    {title: 'Login', name: 'Login', component: Login, index: 2, icon: 'login' }

  ];

  loggedInPages: PageInterface[] = [
    { title: 'Perfil', name: 'AccountPage', component: AccountPage, icon: 'person' },
    { title: 'Meus Horários', name: 'SupportPage', component: SupportPage, icon: 'help' },
    { title: 'Sair', name: 'TabsPage', component: TabsPage, icon: 'log-out', logsOut: true }
  ];
  loggedOutPages: PageInterface[] = [
    { title: 'Login', name: 'LoginPage', component: LoginPage, icon: 'log-in' },
    { title: 'Sobre', name: 'SupportPage', component: SupportPage, icon: 'help' },
    { title: 'Cadastrar', name: 'SignupPage', component: SignupPage, icon: 'person-add' }
  ];
  about: PageInterface[] = [
    { title: 'Tutorial', name: 'TutorialPage', component: TutorialPage, icon: 'log-in' },
    { title: 'Sobre', name: 'SupportPage', component: SupportPage, icon: 'help' },
  ];
  */
  rootPage: any;
    
  constructor(
    public events: Events,
    public platform: Platform,
    public menu: MenuController,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    public modal : ModalController,
    public toastCtrl: ToastController
  ) {
      this.initializeApp();

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // do whatever you need to do here.
      setTimeout(() => {
        this.splashScreen.hide();
      }, 100);
    });
    this.rootPage = LoginPage;
      let toast = this.toastCtrl.create({
        message: 'Avaliação aberta para ALMOÇO.',
        duration: 3000
      });
      toast.present();
    
  }

  openPage(page: PageInterface) {
    let params = {};
    if (page.index) {
      params = { tabIndex: page.index };
    }
    if (this.nav.getActiveChildNavs().length && page.index != undefined) {
      this.nav.getActiveChildNavs()[0].select(page.index);
    
  } else {
      this.nav.setRoot(page.name, params).catch((err: any) => {
        console.log(`Didn't set nav root: ${err}`);
      });
    }
  }

  listenToLoginEvents() {
    this.events.subscribe('user:login', () => {
      this.enableMenu(true);
    });

    this.events.subscribe('user:signup', () => {
      this.enableMenu(true);
    });

    this.events.subscribe('user:logout', () => {
      this.enableMenu(false);
    });
  }

  enableMenu(loggedIn: boolean) {
    this.menu.enable(loggedIn, 'loggedInMenu');
    this.menu.enable(!loggedIn, 'loggedOutMenu');
  }

  platformReady() {
    this.platform.ready().then(() => {
      this.splashScreen.hide();
    });
  }

  isActive(page: PageInterface) {
    let childNav = this.nav.getActiveChildNavs()[0];

    // Tabs are a special case because they have their own navigation
    if (childNav) {
      if (childNav.getSelected() && childNav.getSelected().root === page.tabComponent) {
        return 'primary';
      }
      return;
    }

    if (this.nav.getActive() && this.nav.getActive().name === page.name) {
      return 'primary';
    }
    return;
  }
  openModal(){
    const modal = this.modal.create(ModalPage);
    modal.present();

  }
  
}






